.resume-content__download {
  padding: 0.8rem 2rem;
  font-size: 1.2rem;
  background-color: #e23838;
  color: #fff;
  border: none;
  border-radius: 0.5rem;
  margin: 0.5rem;
  cursor: pointer;
  transition: 0.5s;
}

.resume-content__download:hover {
  background-color: #fff;
  color: #000;
  border: 1px solid #000;
  transform: scale(1.1);
}

.resume-content__dl {
  margin-left: auto;
  align-items: flex-end;
  margin-right: 10rem;
}
.resume-content {
  padding: 100px 0;
  text-align: center;
  width: 100%;
  height: 100%;
}

.resume-content__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.resume-content__header {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 100%;
}
.resume-content__title {
  font-size: 3rem;
  font-weight: 700;
  margin: 1rem 0;
  align-self: start;
  margin-left: 10rem;
}

.resume-content_img {
  margin-top: 1rem;
}

@media screen and (max-width: 1040px) {
  .resume-content__title {
    font-size: 2rem;
    font-weight: 700;
    margin: 1rem 0;
    align-self: start;
    margin-left: 10rem;
  }

  .resume-content__dl {
    margin-left: auto;
    align-items: flex-end;
    margin-right: 10rem;
  }
  .resume-content {
    padding: 100px 0;
    text-align: center;
    width: 100%;
    height: 100%;
  }

  .resume-content_img {
    margin-top: 1rem;
    width: 100%;
    height: 100%;
  }
}
